/* src/pages/CodingProjects.module.css */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
  min-height: 100vh;
  background: #f8fafc;
}

.githubSection {
  margin-bottom: 4rem;
  animation: fadeIn 0.6s ease-out;
}

.githubCard {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  text-decoration: none;
  color: inherit;
  max-width: 800px;
  margin: 0 auto;
}

.githubCard:hover {
  transform: translateY(-3px);
}

.githubImage {
  width: 120px;
  height: 120px;
  margin-right: 2rem;
  border-radius: 12px;
  overflow: hidden;
  flex-shrink: 0;
}

.githubImage img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.githubContent h2 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  color: #2d3748;
}

.githubContent p {
  color: #4a5568;
  line-height: 1.6;
}

.heading {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 4rem;
  color: #2d3748;
  animation: fadeIn 0.6s ease-out;
}

.projectsGrid {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
